.correct {
  color: green
}

.notExist {
  color: black
}

.incorrect {
  color: red
}

.next {
  color: blue!important;
}

.ui.button {
  border-radius: 0px;
  border: 1px green solid;
}

.ui.input>input {
  border-radius: 0px;
}

.wrapper {
  padding-left: 5%;
  padding-right: 5%;
}

.ui.menu {
  border-radius: 0px;
  box-shadow: none;
}

.buttonNext {
  background-color: blue!important;
  color: white!important;
}

#mainText {
  font-size: 15px;
  padding: 30px;
  text-align: center;
}

#inputAndButton {
  width: 300px;
  margin: auto;
}

#currentWord {
  font-size: 20px;
  text-align: center;
  padding-bottom: 30px;
}

.ui.card {
  width: 150px;
  margin: auto;
  text-align: center;
  margin-bottom: 30px;
}

#currentWordPromp {
  background-color: #e0e1e2;
}

#currentWordDis {
  padding: 10px;
  padding-bottom: 5px;
}

#virtualKeyBoard {
  margin-top: 30px;
}

.ui.celled.grid>.column:not(.row), .ui.celled.grid>.row>.column {
  box-shadow: none;
}

.handBox {
  text-align: center;
}

.hand {
  width: 200px;
}
